.featured {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction:row;
}

.featuredItem {
  flex: 1;
  margin: 0px 20px;
  padding: 30px;
  border-radius: 10px;
  cursor: pointer;
  -webkit-box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
}

.featuredTitle {
  font-size: 18px;
  color: var(--deep-light);
  text-transform: uppercase;
}

.featuredMoneyContainer {
  margin: 10px 0px;
  display: flex;
  align-items: center;
}

.featuredMoney {
  font-size: 30px;
  font-weight: 600;
}

.featuredMoneyRate {
  display: flex;
  align-items: center;
  margin-left: 20px;
}

.featuredIcon {
  font-size: 14px;
  margin-right: 10px;
  color: var(--secondary);
}

.featuredSub {
  font-size: 15px;
  color: gray;
}

/* THE SECTION OF THE MEDIA QUERY */
@media screen and (max-width:650px){
  .featured{
    flex-direction:column;
  }
}