@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=DM+Sans:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mukta:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css?family=DM Sans:300,400,700,900);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0%;
}

body {
  font-family: 'Montserrat';
}

/* THE SECTION OF THE GLOBAL VARIABLES */
:root {
  /* THE COLOURS */
  --white: #fff;
  --primary: #3b34b6;
  --light-blue: #2ea2db;
  --deep-blue: #157bad;
  --secondary: #f16051;
  --tetiary: #193651;
  --lighter: #f5f1f1;
  --light: #e5e5e5;
  --deep: #616161;
  --main: #f29c1f;
  --black: #000;
  --hover: #00ff00;

  /* THE FONT-FAMILIES */
  --mont: 'Montserrat';
  --sans: 'DM Sans';
  --mukta: 'Mukta';

  /* SHADOWS AND TRANSITIONS */
  --shadows: 0px 22px 24px rgba(65, 111, 244, 0.2);
  --shadoos: 0px 0px 7px rgba(65, 111, 65, 0.2);
  --shadow: 15px 15px 25px #ddd;
  --transition: all 0.3s linear;
}

a {
  text-decoration: none;
}

Link {
  text-decoration: none;
}

ul {
  list-style-type: none;
}

.loadings {
  height: 40vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.loadingss {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.text-center {
  /* it will be used as utility class */
  text-align: center;
}
select,
button.form-control:focus,
input.form-control:focus,
textarea.form-control:focus,
select.form-control:focus,
input[type='text']:focus,
input[type='password']:focus,
input[type='email']:focus,
input[type='checkbox']:focus,
input[type='number']:focus,
[type='text'].form-control:focus,
[type='password'].form-control:focus,
[type='email'].form-control:focus,
[type='tel'].form-control:focus,
[type='submit'].form-control:focus,
[contenteditable].form-control:focus {
  box-shadow: inset 0 -1px 0 #ddd !important;
}

input[type='checkbox'] {
  height: 5px !important;
}

input,
select {
  padding: 11px !important;
  height: 50px !important;
}

button:focus {
  box-shadow: inset 0 -1px 0 #ddd !important;
}

table th,
td {
  border: 1px solid #e5e5e5 !important;
  border: 1px solid var(--light) !important;
}

table th {
  color: #193651;
  color: var(--tetiary);
}

.responsive {
  padding: 30px 0 60px 0;
  flex: 4 1;
  width: 100%;
  margin-left: 0px;
  overflow: auto;
  transition: all 0.3s linear;
  transition: var(--transition);
}

.response {
  padding: 30px 0 60px 0;
  flex: 4 1;
  width: 100%;
  margin-left: 0;
  transition: all 0.3s linear;
  transition: var(--transition);
}

@media screen and (min-width: 900px) {
  .responsive {
    width: calc(100% - 250px);
    margin-left: 230px;
  }
}

.eye {
  position: absolute;
  top: 40%;
  right: 20px;
  cursor: pointer;
  opacity: 0.5;
  font-size: 1px !important;
  -webkit-transform: translateY(40%);
          transform: translateY(40%);
}

.visible {
  font-size: 20px !important;
}

.sidebar {
  position: fixed;
  top: 70px;
  width: 100%;
  max-width: 230px;
  z-index: 999999;
  height: 100%;
  background: var(--white);
  -webkit-transform: translate(0);
          transform: translate(0);
  transition: var(--transition);
  overflow: auto;
  padding-bottom: 50px;
  box-shadow: var(--shadoos);
}

.hide-sidebar {
  -webkit-transform: translate(-100%);
          transform: translate(-100%);
  transition: var(--transition);
}

.sidebar::-webkit-scrollbar {
  height: 50px;
}

.sidebar::-webkit-scrollbar-thumb {
  /* background: var(--deep-blue); */
  height: 100px;
  border-radius: 50px;
}

.sidebarWrapper {
  padding: 20px 5px;
  color: #555;
  transition: var(--transition);
}

.sidebarMenu {
  margin-bottom: 10px;
  transition: var(--transition);
}

.sidebarTitle {
  font-size: 13px;
  color: rgb(187, 186, 186);
}

.sidebarList {
  list-style: none;
  padding: 5px;
  transition: var(--transition);
}

.sidebarList a {
  text-decoration: none;
  color: var(--deep);
}

.sidebarListItem {
  padding: 10px 5px;
  cursor: pointer;
  display: flex;
  align-items: center;
  border-radius: 5px;
  margin-bottom: 10px;
  color: var(--white);
  transition: var(--transition);
  font-size: 14px;
  color: var(--deep);
}

.active {
  background-color: var(--lighter);
  color: var(--primary) !important;
  transition: var(--transition);
}

.sidebarListItem:hover {
  color: var(--secondary);
}

.sidebarIcon {
  margin-right: 5px;
  font-size: 17px !important;
}

.topbar {
  width: 100%;
  height: 70px;
  background-color: white;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 999;
  box-shadow: var(--shadoos);
}

.topbarWrapper {
  height: 100%;
  padding: 0px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.topLeft {
  width: 100%;
  max-width: 210px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.topbar-toggle {
  background: transparent;
  outline: none;
  border: none;
  color: var(--primary);
}

.topbar-tog {
  background: transparent;
  outline: none;
  border: none;
  color: var(--primary);
  display: none;
}

.topLeft img {
  max-width: 100px;
  max-height: 70px;
  object-fit: cover;
}

.topRight {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.topbarIconContainer {
  position: relative;
  cursor: pointer;
  margin-right: 25px;
  color: #555;
}

.topIconBadge {
  width: 15px;
  height: 15px;
  position: absolute;
  top: -5px;
  right: 0px;
  background-color: red;
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 10px;
}

.topAvatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  cursor: pointer;
  margin-left: 30px;
}

@media screen and (max-width: 650px) {
  .topAvatar {
    display: none;
  }

  .topLeft {
    width: 100%;
    max-width: 100%;
  }
}

.featured {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction:row;
}

.featuredItem {
  flex: 1 1;
  margin: 0px 20px;
  padding: 30px;
  border-radius: 10px;
  cursor: pointer;
  box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
}

.featuredTitle {
  font-size: 18px;
  color: var(--deep-light);
  text-transform: uppercase;
}

.featuredMoneyContainer {
  margin: 10px 0px;
  display: flex;
  align-items: center;
}

.featuredMoney {
  font-size: 30px;
  font-weight: 600;
}

.featuredMoneyRate {
  display: flex;
  align-items: center;
  margin-left: 20px;
}

.featuredIcon {
  font-size: 14px;
  margin-right: 10px;
  color: var(--secondary);
}

.featuredSub {
  font-size: 15px;
  color: gray;
}

/* THE SECTION OF THE MEDIA QUERY */
@media screen and (max-width:650px){
  .featured{
    flex-direction:column;
  }
}
.home {
  padding-top: 30px;
  flex: 4 1;
  width: calc(100% - 250px);
  margin-left: 230px;
  overflow: auto;
  transition: var(--transition);
}

.homeWidgets {
  display: flex;
  margin: 20px;
}


@media screen and (max-width:900px){
  .home {
    width:100%;
    margin-left: 0px;
  }
}
.new_user {
  padding: 30px 20px;
}

.new_user h2 {
  font-size: 19px;
  margin-bottom: 15px;
  color: var(--secondary);
}


@media screen and (max-width:650px){
  
}

.navbar {
  box-shadow: var(--shadoos);
  width: 100%;
  position: fixed !important;
  display: flex;
  justify-content: space-between;
  z-index: 999;
  background: var(--white);
  /* height: 70px; */
}

.header-img {
  width: 60px;
  height: 60px;
  object-fit: cover;
}

.display-menu {
  display: none;
  width: 55%;
  align-items: center;
  justify-content: space-between;
}

.display-menu .menu-btn {
  border: none;
  background: transparent;
  outline: none;
}

/* THE DROPDOWN SECTION */
.navbar-nav .dropdown {
  background-color: var(--white);
  font-size: 18px;
  line-height: 30px;
  box-shadow: var(--shadoos);
  cursor: pointer;
  margin: 10px auto;
  width: 50%;
  transition: var(--transition);
}

.navbar-nav .menu-icon {
  margin-top: 3px;
  color: var(--tetiary);
}

.navbar-nav .dropdown .nav-link span {
  font-family: var(--mont);
  font-weight: 500;
  color: var(--tetiary);
  font-size: 17px;
}

.login-box a {
  background-color: var(--white);
  text-decoration: none;
  padding: 12px;
  color: var(--primary);
  font-family: 'Montserrat';
  font-weight: 600;
  font-size: 13px;
  transition: var(--transition);
  cursor: pointer;
  border: 1px solid var(--light);
}

.login-box a:last-child {
  background-color: var(--primary);
  color: var(--white);
  margin-left: 10px;
}

.login-box-icon {
  margin-right: 5px;
  font-size: 17px !important;
}

/* -=============================================-- */
/* THE SECTION OF THE DROPDOWN */
.main-dropdown {
  padding: 0;
  margin-top: 10px;
  cursor: pointer;
}

.drop-nav {
  position: relative;
  right: 20px;
  margin: 0;
}

.drop-nav a {
  text-transform: capitalize;
  overflow: hidden;
  text-decoration: none;
}

.drop-div {
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
}

.drop-div .drop-flex {
  display: flex;
  align-items: center;
  margin-top: 5px;
  margin-right: 20px;
  position: relative;
}

.notificate {
  background: var(--white);
  position: absolute;
  top: 50px;
  right: -50px;
  width: 240px;
  padding: 15px;
  line-height: 25px;
  box-shadow: var(--shadoos);
  border-radius: 10px;
  text-align: center;
}

.notificate .notification {
  color: var(--secondary);
  font-size: 10px;
}

.notificate button {
  font-size: 11px;
}

.topbarIconContainer {
  position: relative;
  cursor: pointer;
  margin-right: 15px;
  color: #555;
  width: 100%;
}

.topIconBadge {
  width: 15px;
  height: 15px;
  position: absolute;
  top: -5px;
  right: 0px;
  background-color: red;
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 10px;
}

/* =================================== */
.drop-nav .dropdown {
  position: absolute;
  background: var(--white);
  box-shadow: var(--shadoos);
  width: 50% !important;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, 0%);
          transform: translate(-50%, 0%);
  display: block;
  z-index: 9999;
  padding: 12px;
  border-radius: 5px;
}

.dropdown .drop-header {
  text-align: center;
  margin-bottom: 20px;
}

.drop-left {
  border-radius: 50%;
  height: 60px;
  width: 60px;
  border: 2px solid var(--primary);
  margin: 0 auto 10px auto;
  position: relative;
}

.drop-left img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  /* object-fit: cover; */
}

.drop-right h5 {
  font-size: 14px;
  text-transform: uppercase;
  color: var(--deep);
}

.drop-right span {
  font-size: 12px;
  line-height: 10px;
  display: block;
  color: var(--primary);
}

.avatar {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  border: 1px solid var(--tetiary);
  position: relative;
}

.avatar img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  object-fit: cover;
}

.drop-nav .dropdown li {
  display: block;
  font-size: 14px;
  padding: 7px;
  border-radius: 5px;
  cursor: pointer;
  color: var(--tetiary);
  font-weight: 500;
}

.dropdown-menu a {
  text-decoration: none;
}

.top-icon {
  margin: -3px 5px 0 0;
  font-size: 16px !important;
}

.drop-nav .dropdown li:hover {
  background: var(--light);
}

.become {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 23%;
}

.become .become-link:hover {
  text-decoration: none;
  background: transparent;
}

.become a {
  text-decoration: none;
}

.become-link {
  position: relative;
}

/* ================================================== */
@media screen and (max-width: 990px) {
  .display-menu {
    display: flex;
  }

  .container-fluid .menu-btn {
    display: block;
    float: right;
  }

  .login-box {
    text-align: center;
    margin-top: 20px;
  }

  .drop-flexer {
    display: flex;
    align-items: center;
  }

  .drop-div .drop-flex {
    display: none;
  }

  .notificate {
    top: 110px;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 220px;
    z-index: 999;
  }

  .badge-icon {
    font-size: 20px !important;
  }
}

@media screen and (min-width: 988px) {
  .navbar-nav .dropdown {
    width: 100%;
    margin-left: 80px;
  }

  .drop-div {
    width: 100%;
  }
}

/* ======================================================= */
@media screen and (min-width: 1000px) {
  .navbar {
    height: 80px;
  }

  .header-img {
    height: 70px;
    width: 70px;
  }

  /* THE DROPDOWN SECTION */
  .navbar-nav .dropdown {
    margin-left: 250px;
  }

  .drop-div {
    width: 100%;
    align-items: center;
    justify-content: space-between;
  }

  .drop-nav .dropdown {
    position: absolute;
    background: var(--white);
    box-shadow: var(--shadoos);
    width: 180% !important;
    right: -15px;
    left: 30px;
    top: 40px;
    /* display: none; */
    z-index: 9999;
    padding: 12px;
    border-radius: 5px;
  }
}

/* ========================================================= */
/* THE SECTION OF THE MEDIA QUERY */
@media screen and (max-width: 768px) {
  .header-img {
    width: 50px;
    height: 50px;
    margin-top: -7px;
  }

  .navbar-nav .dropdown {
    width: 90%;
    margin-left: auto;
  }

  .navbar-nav .menu-icon {
    margin: 3px 10px !important;
  }

  .login-box {
    margin-bottom: 20px;
  }

  .drop-nav {
    left: 5px;
  }

  .drop-div {
    width: 50%;
    justify-content: center;
    margin: auto;
  }

  .drop-nav .dropdown {
    width: 90% !important;
    left: 49%;
  }
}

/* MEDIA QUERY FOR SMALLER DEVICES */
@media screen and (max-width: 425px) {
  .navbar-nav .dropdown {
    width: 90%;
    margin-left: auto;
  }

  .navbar-nav .menu-icon {
    margin: 3px 10px !important;
  }

  .login-box {
    margin-bottom: 20px;
  }

  .drop-nav {
    left: 5px;
  }

  .drop-div {
    width: 100%;
    justify-content: center;
    margin: auto;
  }

  .drop-nav .dropdown {
    width: 90% !important;
    left: 49%;
  }
}

#footer {
  color: var(--white);
  font-size: 14px;
  background: var(--tetiary);
}

#footer .footer-top {
  padding: 60px 0 30px 0;
  background: var(--tetiary);
}

#footer .footer-top .footer-contact {
  margin-bottom: 30px;
}

#footer .footer-top .footer-contact h3 {
  font-size: 26px;
  line-height: 1;
  font-weight: 700;
}

#footer .footer-top .footer-contact h3 span {
  color: var(--secondary);
}

#footer .footer-top .footer-contact p {
  font-size: 14px;
  line-height: 24px;
  margin-bottom: 0;
  color: var(--light);
}

#footer .footer-top h4 {
  font-size: 16px;
  font-weight: bold;
  position: relative;
  padding-bottom: 12px;
}

#footer .footer-top h4::after {
  content: '';
  position: absolute;
  display: block;
  width: 20px;
  height: 2px;
  background: var(--secondary);
  bottom: 0;
  left: 0;
}

#footer .footer-top .footer-links {
  margin-bottom: 30px;
}

#footer .footer-top .footer-links ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

#footer .footer-top .footer-links ul i {
  padding-right: 2px;
  color: var(--white);
  font-size: 18px;
  line-height: 1;
}

#footer .footer-top .footer-links ul li {
  padding: 10px 0;
  display: flex;
  align-items: center;
}

#footer .footer-top .footer-links ul li:first-child {
  padding-top: 0;
}

#footer .footer-top .footer-links ul a {
  color: #aaaaaa;
  transition: 0.3s;
  display: inline-block;
  line-height: 1;
  text-decoration: none;
}

#footer .footer-top .footer-links ul a:hover {
  text-decoration: none;
  color: var(--white);
}

#footer .footer-newsletter {
  font-size: 15px;
}

#footer .footer-newsletter h4 {
  font-size: 16px;
  font-weight: bold;
  position: relative;
  padding-bottom: 12px;
}

#footer .footer-newsletter form {
  margin-top: 30px;
  background: var(--white);
  padding: 0px 10px;
  position: relative;
  border-radius: 4px;
  text-align: left;
  /* height: 50px; */
}

#footer .footer-newsletter form input[type='email'] {
  border: 0;
  padding: 34px 8px;
  width: calc(100% - 100px);
  outline: none;
  border: none !important;
  background: transparent;
  height: 30px;
}

#footer .footer-newsletter form input[type='submit'] {
  position: absolute;
  top: 0;
  height: 30px;
  right: -1px;
  bottom: 0;
  border: 0;
  background: none;
  font-size: 16px;
  padding: 0 20px;
  background: var(--primary);
  color: var(--white);
  transition: 0.3s;
  border-radius: 0 4px 4px 0;
  box-shadow: 0px 2px 15px rgba(0, 0, 0, 0.1);
  outline: none;
}

#footer .footer-newsletter form input[type='submit']:hover {
  background: var(--secondary);
}

#footer .credits {
  padding-top: 5px;
  font-size: 13px;
}

#footer .social-links a {
  font-size: 18px;
  display: inline-block;
  background: var(--tetiary);
  color: var(--white);
  text-decoration: none;
  line-height: 1;
  padding: 8px 0;
  margin-right: 4px;
  border-radius: 4px;
  text-align: center;
  width: 36px;
  height: 36px;
  transition: 0.3s;
}

#footer .social-links a:hover {
  background: var(--secondary);
  color: var(--white);
  text-decoration: none;
}

.footer-bot {
  border-top: 1px solid var(--white);
}

@media screen and (max-width: 600px) {
  #footer .footer-newsletter p {
    font-size: 13px;
  }
}

.errMsg {
  color: red;
  text-align: center;
  padding: 10px 0;
  letter-spacing: 1.3px;
  font-size: 14px;
}

.successMsg {
  color: green;
  text-align: center;
  padding: 10px 0;
  letter-spacing: 1.3px;
  font-size: 14px;
}

.login {
  display: flex;
  align-items: center;
  height: 100%;
  justify-content: center;
}

.login-center {
  width: 90%;
  margin: 0 auto;
  display: flex;
  flex-direction: column !important;
  align-items: center;
  justify-content: center;
  padding: 150px 0;
}

.login-details {
  margin-top: 10px;
  margin-bottom: 10px;
  background: var(--light);
  border-radius: 10px;
  padding: 10px;
}

.login-details h5 {
  font-size: 15px;
}

.login-details small {
  display: block;
}

.login-center .list-group-item.active {
  background: var(--light) !important;
  outline: none !important;
  border: none !important;
  color: var(--secondary) !important;
}

.login-center label {
  color: var(--primary);
}

.login-center input::-webkit-input-placeholder {
  font-size: 14px;
}

.login-center input,
.login-center select {
  font-size: 14px;
}

.login-center button {
  background: var(--primary);
  color: var(--white);
  text-transform: uppercase;
  width: 100%;
}

.login-center button:hover {
  color: var(--white);
  background: var(--primary);
}

.login-center .pass .eyes {
  position: absolute;
  top: 40%;
  right: 20px;
  cursor: pointer;
  opacity: 0.5;
  font-size: 15px;
  -webkit-transform: translateY(50%);
          transform: translateY(50%);
}

.login-center .pass {
  position: relative;
}

.login-center .group ::-webkit-input-placeholder {
  font-size: 13px;
}

.login-center .form-check input {
  border: 1px solid var(--light) !important;
  height: 18px;
  width: 18px;
  margin-right: 5px;
  border-radius: 15px;
  appearance: none;
