  /* height: 100vh; */
  display: flex;
  align-items: center;
  flex-direction: column;
}

.roadmap-top {
  background: var(--tetiary);
  color: var(--white);
  text-align: center;
  width: 100%;
  padding: 50px 0;
  margin-bottom: 20px;
}

.road-center {
  width: 90%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1rem;
  background: var(--lighter);
  padding: 20px;
}

.road-center a {
  text-decoration: none;
}

.road-card {
  border-radius: 15px;
  padding: 10px;
  cursor: pointer;
  color: var(--white);
}

.road-card h3 {
  font-size: 20px;
  font-weight: bold;
}

.road-card p {
  font-size: 15px;
}

/* THE SECTION OF THE MEDIA QUERY */
@media screen and (min-width: 900px) {
  .road-center {
    width: 70%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1rem;
    background: var(--lighter);
    padding: 20px;
  }
}

@media screen and (max-width: 500px) {
  .road-center {
    width: 95%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    padding: 10px;
  }

  .roadmap-top h4 {
    font-size: 17px;
  }
}

.road-details {
  padding: 75px 0;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.road-details-top {
  background: var(--tetiary);
  color: var(--white);
  text-align: center;
  width: 100%;
  padding: 50px 0;
  margin-bottom: 20px;
  text-transform: uppercase;
}

.road-details-center {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1rem;
  padding: 20px;
  text-align: center;
  overflow: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.road-image {
  width: 1200px;
  height: 100%;
  margin: 0 auto;
  display: block;
}

.road-image img {
  width: 100%;
  height: 100%;
  border: 1px solid red;
}

.contact-us {
  padding-top: 80px;
}

.callback {
  display: flex;
  flex-direction: column;
  background: var(--light);
  /* padding-bottom: 5rem; */
}

.callback-left img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* THE SECTION OF THE CALLBACK RIGHT */
.callback-right {
  display: block;
  padding: 30px 60px;
}

.callback label {
  display: block;
  padding: 20px 0 5px;
  font-family: DM Sans;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
  letter-spacing: -0.6px;
  color: var(--primary);
}

.callback-contact {
  overflow: hidden;
}

.form-group {
  width: 100%;
}

.callback-contact input {
  width: 100%;
  padding: 15px;
  border: none;
  outline: none;
  resize: none;
}

.callback input::-webkit-input-placeholder {
  font-size: 12px;
}

.callback textarea {
  height: 150px;
  width: 100%;
  padding: 15px;
  border: none;
  outline: none;
  resize: none;
}

.callback .btn {
  background: var(--primary);
  color: var(--white);
  padding: 18px;
  width: 100%;
  border: none;
  outline: none;
  margin-top: 15px;
  cursor: pointer;
  font-weight: bold;
  transition: var(--transition);
  font-size: 15px;
}

.callback .btn:hover {
  color: var(--white);
  background: var(--secondary);
}

.callback .msg {
  display: none;
  color: green;
  text-align: center;
}

.callback .msgAppear {
  display: block;
}

@media screen and (min-width: 900px) {
  .callback {
    flex-direction: row;
  }

  .callback-left {
    width: 60%;
  }

  .callback-right {
    width: 40%;
  }

  .callback textarea {
    padding: 10px;
    width: 100%;
  }
}

@media screen and (min-width: 200px) and (max-width: 425px) {
  .callback-right {
    padding: 30px 10px;
  }
}

